@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body{margin:0;box-sizing:border-box;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:inherit}a{text-decoration:inherit;color:inherit}


.header{background:#0B0B3B;z-index:1;height:5rem;display:flex;align-items:center;position:fixed;top:0;left:0;right:0;box-shadow:0 2px 2px dimgrey}.header .logo{color:white;font-weight:800;font-size:2rem;cursor:pointer;margin-left:2rem;-webkit-user-select:none;-ms-user-select:none;user-select:none}.header .description{font-size:0.8rem;font-weight:500}.header .logout{margin-left:auto;margin-right:1rem;margin-bottom:0;color:white;text-align:center;font-weight:800;font-size:2rem;cursor:pointer}.header .logoutText{color:white;margin-top:0;font-weight:80;font-size:0.8rem}

.PersonBar{width:240px;height:300px;padding:0;margin:0;border:1px}.PersonBar .PersonImage{margin:30px 50px;width:140px;height:140px;border-radius:30%;padding:20px;background-color:aliceblue}.PersonBar .PersonName{height:60px;font-size:30px;font-weight:bold;text-align:center;line-height:60px;border-top:1px solid black;border-bottom:1px solid black}.PersonBar .ChangeInfo{line-height:40px;text-align:center;text-decoration:underline;cursor:pointer;border-bottom:1px solid black}

.Button{height:5rem;width:10rem;right:5%;bottom:5%;position:fixed;border:5px solid #0B0B3B;border-radius:0.5em;background-color:white;margin-left:1rem;margin-right:1rem;cursor:pointer;box-shadow:3px 3px 3px 3px darkgrey;font-size:1.5em;color:#0B0B3B;line-height:5rem;text-align:center}.Button:hover{background-color:#0B0B3B;box-shadow:3px 3px 3px 3px darkgrey;color:black}

.Content{background-color:white;width:40%;height:50%;border-radius:0.5em;position:absolute;left:30%;top:25%}

